import Render from './render.js';
import Slide from './slide.js';
import Request from './request.js';
import Resize from './resize.js';
import GASection from './renderGA';

class Main {
	constructor() {
        this.GASection = new GASection();
		this.render = new Render();
		this.request = new Request();
		this.slide = new Slide();
		this.resize = new Resize();

		this.render.renderPage();
		this.slide.addListeners(this.render);
	}

	addSearchListeners() {
		//--------------------------------------- event listener for 'Enter' key --------------------------------
        document.querySelector('#search').addEventListener('keypress', function(e) {
            if (e.keyCode === 13 && this.value) {
				Slide.slidePos = [0];
				main.request.chooseVideosCount();
				document.querySelector('.notFound').style.display = 'none';
				document.querySelector('.wrapper').innerHTML = '';
				document.querySelector('#pages').innerHTML = '';
				main.render.renderFirstSlides();

				main.request.initialization(main.render, Slide.slidePos);
				Slide.currentPage = 0;
                
                if(newSearch){
                    let wrapper = document.querySelector('.wrapper');
                    let pages = document.querySelector('#pages');
                    while(wrapper.firstChild) wrapper.removeChild(wrapper.firstChild);
                    while(pages.firstChild) pages.removeChild(pages.firstChild);
                }
                let newSearch = true;
			}
            else if(e.keyCode === 13 && !this.value){
                showInfo('please enter search keyword');
            }
		});
        //------------------------------------- event listener for SEARCH button -------------------------------
        document.querySelector('.btn-search').addEventListener('click', function(e) {
                if(document.querySelector('#search').value){
                    Slide.slidePos = [0];
                    main.request.chooseVideosCount();
                    document.querySelector('.notFound').style.display = 'none';
                    document.querySelector('.wrapper').innerHTML = '';
                    document.querySelector('#pages').innerHTML = '';
                    main.render.renderFirstSlides();

                    main.request.initialization(main.render, Slide.slidePos);
                    Slide.currentPage = 0;
                    
                    if(newSearch){
                        let wrapper = document.querySelector('.wrapper');
                        let pages = document.querySelector('#pages');
                        while(wrapper.firstChild) wrapper.removeChild(wrapper.firstChild);
                        while(pages.firstChild) pages.removeChild(pages.firstChild);
                    }
                    let newSearch = true;
                } else showInfo('please enter search keyword');
        });

	}
}

window.main = new Main();
window.main.addSearchListeners();



